<template>
  <div>
    <el-card class="box-card-postSong">
      <div slot="header" class="clearfix"></div>
      <section class="card-content-one">
        <el-upload
          class="upload-song"
          drag
          :limit="1"
          action
          :on-change="handleUpLoadMp3Change"
          :auto-upload="false"
          ref="uploadSong"
        >
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">
            mp3
            <em></em>
          </div>
          <div class="el-upload__tip" slot="tip">mp315m</div>
        </el-upload>
        <div class="center-input-block">
          <span></span>
          <el-input v-model="audio_name" placeholder=""></el-input>
          <span></span>
          <el-input v-model="album_name" placeholder=""></el-input>
          <span></span>
          <el-input v-model="singer_name" placeholder=""></el-input>
        </div>
        <div class="right-commit-block">
          <el-button type="primary" class="send_song" @click="sendSongFile"></el-button>
        </div>
      </section>
      <section class="card-content-to">
        <el-upload
          class="avatar-uploader albumImg-uploader"
          action
          :on-change="handleUpLoadImageChange"
          :show-file-list="false"
          :auto-upload="false"
          ref="uploadAlbumImg"
        >
          <div
            v-if="imageUrl"
            :style="{backgroundImage:`url(${imageUrl})`}"
            class="postSong_album_img"
          ></div>
          <!-- <img v-if="imageUrl" :src="imageUrl" class="avatar" /> -->
          <div class="el-upload__text">()</div>
        </el-upload>
        <audio class="browse_audio" :src="mp3Url" controls></audio>
      </section>
    </el-card>
  </div>
</template>

<script>
import axios from "axios";
import { mapActions } from "vuex";
export default {
  data: () => ({
    mp3File: null,
    imageFile: null,
    audio_name: "",
    album_name: "",
    singer_name: "",
    mp3Url: "",
    imageUrl: "",
    progress: ""  // 
  }),
  methods: {
    ...mapActions(["receiveSongList", "modifySongIndex"]),
    isJPGLt2M(imageFile) {
      if (!imageFile) return false;
      const isLt2M = imageFile.raw.size / 1024 / 1024 < 2;
      if (!isLt2M) this.$message.error(" 2MB!");
      return isLt2M;
    },
    isMP3Lt15M(mp3File) {
      if (!mp3File) {
        this.$message.error("!");
        return false;
      }
      const isMP3 = mp3File.raw.type.match(/audio\/mpeg|audio\/mp3/);
      const isLt5M = mp3File.raw.size / 1024 / 1024 < 15;
      if (!isMP3) {
        this.$message.error(" mp3 !");
        return false;
      }
      if (!isLt5M) {
        this.$message.error(" 15MB!");
        return false;
      }
      return isMP3 && isLt5M;
    },
    async sendSongFile() {
      // 
      const { $message, audio_name, submitFile, isMP3Lt15M, mp3File } = this;
      // mp3, 
      if (!isMP3Lt15M(mp3File)) return false;
      // 
      if (!audio_name) return $message.error(" !");
      const loadingInstance = this.$loading({
        background: "rgba(255,255,255,.2)",
        text: "...."
      });
      const result = await submitFile();
      loadingInstance.close();
      if (result && result.code !== 0) return this.$message.error(result.msg);
      // , , , 
      this.$message({ type: "success", message: "" });
      this.remoteData();
      this.receiveSongList([result.data]);
      this.modifySongIndex(0);
    },
    remoteData() {
      // 
      this.$refs.uploadSong.uploadFiles = [];
      this.$refs.uploadAlbumImg.uploadFiles = [];
      this.imageUrl = "";
      this.mp3Url = "";
      this.audio_name = "";
      this.album_name = "";
      this.singer_name = "";
    },
    submitFile() {
      // append  FormData 
      const {
        mp3File,
        audio_name,
        album_name,
        singer_name,
        imageFile
      } = this;
      // , MP3
      const fromData = new FormData();
      fromData.append("audio", mp3File.raw);
      // , fromData
      if (imageFile && imageFile.raw) {
        const isLt2M = imageFile.raw.size / 1024 / 1024 < 2;
        if (isLt2M) fromData.append("album_image", imageFile.raw);
      }
      // , urlid,
      return axios({
        url: "/upload_song",
        headers: { "Content-Type": "multipart/form-data" },
        method: "POST",
        data: fromData,
        // 
        onUploadProgress: e => {
          const completeProgress = (((e.loaded / e.total) * 100) | 0) + "%";
          this.progress = completeProgress
        },
        params: {
          audio_name: audio_name.trim(),
          album_name: album_name.trim(),
          singer_name: singer_name.trim()
        }
      });
    },
    handleUpLoadMp3Change(file) {
      // MP3, this
      this.mp3File = file;
      const isMP3 = file.raw.type.match(/audio\/mpeg|audio\/mp3/);
      if (!isMP3) return false;
      this.audio_name = file.raw.name.split(".")[0];
      this.mp3Url = window.webkitURL.createObjectURL(file.raw);
    },
    handleUpLoadImageChange(file) {
      // image, this
      if (!this.isJPGLt2M(file)) return false;
      this.imageUrl = window.webkitURL.createObjectURL(file.raw);
      this.imageFile = file;
    }
  }
};
</script>

<style lang="less">
.albumImg-uploader {
  width: 200px;
  height: 200px;
  margin: 0 80px;
  .el-upload.el-upload--text {
    width: 200px;
    height: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    .el-upload__text {
      color: #606266 !important;
      font-size: 14px;
      text-align: center;
    }
    .postSong_album_img {
      width: 100%;
      height: 100%;
      background-size: cover;
      background-position: center;
    }
    img {
      width: 200px;
      height: 200px;
    }
  }
}
.browse_audio {
  flex: 1;
  margin-right: 70px;
}
.card-content-to {
  display: flex;
  align-items: center;
}
.card-content-one {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.right-commit-block {
  height: 180px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  float: left;
}
.send_song {
  width: 200px;
  height: 100px;
  span {
    font-weight: bold;
    letter-spacing: 2px;
    font-size: 20px;
    color: #ffffff;
  }
}
.center-input-block {
  .el-input {
    min-width: 100px;
  }
  height: 180px;
  float: left;
  width: 350px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-right: 50px;
  margin-bottom: 30px;
  span {
    font-size: 15px;
    color: rgba(0, 0, 0, 0.7);
  }
}
.el-icon-postSong {
  cursor: pointer;
}
.box-card-postSong {
  margin-top: -5px;
  text-align: left;
  .el-icon-postSong {
    *zoom: 1;
    &:after {
      content: "";
      display: block;
      clear: both;
    }
    .text {
      font-size: 14px;
    }
  }
}
.controls {
  font-size: 25px;
  i {
    margin-right: 25px;
  }
  text-align: right;
}
.upload-song {
  width: 360px;
  margin-right: 50px;
  margin-bottom: 30px;
  float: left;
  .el-upload.el-upload--text {
    width: 360px;
    height: 180px;
  }
}
</style>