// js
// 
import Vue from 'vue';
// 0
const PrefixInteger = (num, n) => (Array(n).join(0) + num).slice(-n)
// date-fns
Vue.filter('date-format', (value) => {
    const minute = PrefixInteger(Math.floor(value / 60), 2)
    const second = PrefixInteger(Math.floor(value % 60), 2)
    return `${minute}:${second}`
})
